<template>
  <div id="wrapper">
    <div id="student-list" v-for="student in students" :key="student.id">
      <span class="student-name">{{ student.studentName }}</span>
      <input type="checkbox" :checked="getAttendance(student)" @change="onAttendance(student, $event)">
    </div>
  </div>
</template>

<script>
import db from "@/script/firestore";
/* eslint-disable no-debugger */
export default {
  name: "AttendanceComponent",
  created() {
    const classId = this.$route.params.classId;
    this.$bind('students', db.collection('chickenClass').doc(classId).collection('data'));
  },
  data() {
    const now = new Date();
    const year = now.getFullYear();
    const month = now.getMonth() + 1;
    const key = `${year}_${month}`;
    const date = now.getDate();
    return {
      students: [],
      errorMessage: '',
      key: key,
      date: date
    }
  },
  computed: {
    studentsRef() {
      return db.collection('chickenClass').doc(this.$route.params.classId).collection('data');
    }
  },
  methods: {
    async onAttendance(student, event) {
      student.data = student.data || {};
      let data = student.data;
      let attendanceMonth = data[this.key] || [];
      if (event.currentTarget.checked) {
        attendanceMonth.push(this.date)
      } else {
        attendanceMonth = attendanceMonth.filter((v) => v !== this.date);
      }
      student.data[this.key] = attendanceMonth;
      try {
        await this.studentsRef.doc(student.id).update({
          data: student.data
        })
      } catch (err) {
        this.errorMessage = err;
      }
    },
    getAttendance(student) {
      if (student.data && student.data[this.key]) {
        return student.data[this.key].includes(this.date)
      }
      return false;
    }
  }
}
</script>

