<template>
  <div id="app">
    <HeaderComponent/>
    <router-view></router-view>
  </div>
</template>

<script>
import VueRouter from 'vue-router'
import Vue from 'vue'
import { firestorePlugin } from 'vuefire'
import VCalendar from 'v-calendar';
import HomeComponent from './components/HomeComponent.vue'
import HeaderComponent from './components/ulti/HeaderComponent.vue'
import ClassDetailComponent from "@/components/ClassDetailComponent";
import EditStudentInfoComponent from "@/components/EditStudentInfoComponent";
import AttendanceComponent from "@/components/AttendanceComponent";
import StudentDataMonthComponent from "@/components/StudentDataMonthComponent";
const routes = [
  {path: '/', component: HomeComponent},
  {
    path: '/class/:classId',
    name: 'class',
    component: ClassDetailComponent
  },
  {
    path: '/class/:classId/edit',
    name: 'edit',
    component: EditStudentInfoComponent
  },
  {
    path: '/class/:classId/attendance',
    name: 'attendance',
    component: AttendanceComponent
  },
  {
    path: '/class/:classId/edit/attendance/:studentId',
    name: 'edit_attendance',
    component: StudentDataMonthComponent
  }
]
const router = new VueRouter({
  routes
})
Vue.use(firestorePlugin);
Vue.use(VCalendar);
Vue.use(VueRouter)
export default {
  router: router,
  name: 'App',
  components: {
    HeaderComponent
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
