<template>
  <div id="wrapper">
    <div>{{ errorMessage }}</div>
    <div id="add-student">
      <input class="input is-primary" type="text" placeholder="Enter Student Name" v-model="newStudentName">
      <button class="button" v-if="!isEdited" @click="addStudent()">Add</button>
    </div>
    <div id="student-list" v-for="student in students" :key="student.id">
      <div v-if="editedStudent && editedStudent.id === student.id">
        <input type="text" v-model="student.studentName" @onblur="editStudent(student)">
        <button @click="editStudent(student)">Save</button>
      </div>
      <div v-else>
        <span class="student-name">{{ student.studentName }}</span>
        <button v-if="!isEdited" @click="onClickEditBtn(student)">EditName</button>
        <button v-if="!isEdited" @click="removeStudent(student.id)">Remove</button>
        <button v-if="!isEdited" @click="editAttendanceData(student.id)">Attendance data</button>
      </div>
    </div>
  </div>
</template>

<script>
import db from "@/script/firestore";

export default {
  name: "EditStudentInfoComponent",
  created() {
    const classId = this.$route.params.classId;
    this.$bind('students', db.collection('chickenClass').doc(classId).collection('data'));
  },
  data() {
    return {
      editedStudent: null,
      isEdited: false,
      students: [],
      newStudentName: '',
      errorMessage: ''
    }
  },
  computed: {
    studentsRef() {
      return db.collection('chickenClass').doc(this.$route.params.classId).collection('data');
    }
  },
  methods: {
    onClickEditBtn(student) {
      if (!this.isEdited) {
        this.editedStudent = student;
        this.isEdited = true;
      }
    },
    async editStudent(student) {
      try {
        if (student.studentName) {
          await this.studentsRef.doc(this.editedStudent.id).update({
            studentName: student.studentName
          });
          this.editedStudent = null;
          this.errorMessage = '';
          this.isEdited = false;
        } else {
          this.errorMessage = 'Name can\'t empty';
        }
      } catch (err) {
        this.errorMessage = err;
      }
    },
    async removeStudent(studentId) {
      try {
        await this.studentsRef.doc(studentId).delete();
      } catch (err) {
        this.errorMessage = err;
      }
    },
    async addStudent() {
      let isExistedStudent = this.students.find((d) => {
        return d === this.newStudentName;
      });
      if (isExistedStudent) {
        this.errorMessage = 'Student already existed';
      } else {
        try {
          if (this.newStudentName) {
            await this.studentsRef.add({
              studentName: this.newStudentName
            });
            this.newStudentName = '';
            this.errorMessage = '';
          } else {
            this.errorMessage = 'Student name can\'t empty';
          }
        } catch (err) {
          this.errorMessage = err;
        }
      }
    },
    editAttendanceData(studentId) {
      const classId = this.$route.params.classId;
      this.$router.push({name: 'edit_attendance', params: {studentId: studentId, classId: classId}});
    }
  }
}
</script>

