<template>
  <div id="class-detail-wrapper">
    <button class="button is-primary is-fullwidth is-block mb-5" @click="gotoEdit()">Edit</button>
    <button class="button is-primary is-fullwidth is-block" @click="gotoAttendance()">Attendance</button>
  </div>
</template>

<script>
/* eslint-disable no-debugger */

export default {
  name: "ClassDetailComponent",
  methods: {
    gotoEdit() {
      const classId = this.$route.params.classId;
      this.$router.push({name: 'edit', params: {classId: classId}});
    },
    gotoAttendance() {
      const classId = this.$route.params.classId;
      this.$router.push({name: 'attendance', params: {classId: classId}});
    }
  }
}
</script>

