<template>
  <div class="home-wrapper">
    <div id="error">{{ errorMessage }}</div>
    <div id="new-class" class="pb-5">
      <input v-model="newClassName" type="text" name="add-new" placeholder="Enter Class Name">
      <button @click="addClass()">save</button>
    </div>
    <div v-for="classData in chickenClass" :key="classData.id">
      <div class="class-wrapper">
        <button @click="selectClass(classData.id)"
                class="button p-0 is-fullwidth is-primary m-5">
          {{ classData.className }}
        </button>
        <!--        <button class="button p-0 column" @click="removeClass(classData.id)">delete</button>-->
      </div>
    </div>
  </div>
</template>

<script>
import db from '../script/firestore'

export default {
  name: 'HomeComponent',
  data() {
    return {
      newClassName: '',
      errorMessage: '',
      chickenClass: [],
    }
  },
  firestore: {
    chickenClass: db.collection('chickenClass'),
  },
  methods: {
    async addClass() {
      const data = (await db.collection('chickenClass')
          .get()).docs.map(doc => doc.data());
      let isExistClass = data.find((v) => {
        return v.className === this.newClassName;
      });
      if (isExistClass) {
        this.errorMessage = 'Class already existed';
      } else if (!this.newClassName) {
        this.errorMessage = "Class name is not empty";
      } else {
        try {
          await db.collection('chickenClass').add({
            className: this.newClassName
          });
          this.newClassName = '';
          this.errorMessage = '';
        } catch (err) {
          this.errorMessage = err;
        }
      }
    },
    // removeClass(id) {
    //   try {
    //     db.collection('chickenClass').doc(id).delete();
    //   } catch (err) {
    //     this.errorMessage = err;
    //   }
    // },
    selectClass(classId) {
      this.$router.push({name: 'class', params: {classId: classId}})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#error {
  color: red;
}
</style>
